body {
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.watch {
  box-sizing: border-box;
  border: 5px dotted #4f94b9;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__circle {
  z-index: 1;
  background-color: orange;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
}

.watch__second-hand, .watch__minute-hand {
  transform-origin: bottom;
  animation: 60s linear infinite rotate-animation;
  position: absolute;
}

.watch__second-hand {
  background-color: green;
  width: 2px;
  height: 50%;
  animation-duration: 5s;
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%);
}

.watch__minute-hand {
  background-color: #00f;
  width: 10px;
  height: 25%;
  top: 25%;
  left: 49.5%;
  transform: translate(-50%, -100%);
}

@keyframes rotate-animation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

